/* DIV-PRODUCTO ES EL DIV DEL PRODUCT CARDS */
.div-producto {
  width: 20%;
  max-width: 1200px;
  height: 600px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}
/* COMPONENTE */
.card {
  width: 300px;
  height: 800px;
  border-radius: 8px;
  box-shadow: 1px 5px 3px 3px rgba(0, 0, 0, 0.2);
  /* overflow: hidden; */
  margin: 70px 20px;
  text-align: center;
  transition: all 0.2s;
}

.card:hover {
  transform: translateY(-1px);
  box-shadow: 0 16px 16px #00000044;
}

/* LETRAS */

.card p {
  color: black;
  /* margin: 10px; */
}

.titulo {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 3px;
  flex-wrap: wrap;
  font-size: 1.4em;
  font-weight: 100;
  color: rgb(19, 18, 18);
  font-family: "Imbue", serif;
}
.stock {
  margin-top: 15px;
  padding: 0;
  font-size: 0.9em;
  color: rgba(158, 22, 22, 0.733);
}

.stock span {
  color: black;
}

.precio {
  color: white;
  font-size: 2.4em;
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  padding: 0;
}

.precio span {
  color: rgba(185, 30, 30, 0.774);
}

/* BOTON */
.boton-editar {
  background-image: url("https://c0.klipartz.com/pngpicture/529/757/gratis-png-iconos-de-la-computadora-tornillo-de-tuerca-thumbnail.png");
  background-size: 20px;
  width: 21px;
  height: 21px;
  border-radius: 50px;
  border: none;
}
/* rgba(185, 30, 30, 0.774) */
.boton-comprar {
  background: linear-gradient(50deg, #2c2424, #ad3131);
  color: white;
  border-radius: 20px;
  height: 31px;
  width: 150px;
  border: none;
  font-family: "Great Vibes", cursive;
  font-size: 25px;
  outline: none;
}

.boton-comprar:hover {
  font-size: 28px;
  height: 34px;
  width: 160px;
}

.div-boton {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 3px;
  outline: none;
  /* background-color: black; */
}

.div-precio {
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.boton-y-stock {
  margin-top: -30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
/* IMAGEN */

.div-imagen {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(330deg, #6b3131e1, #b82323df);
  height: 320px;
  width: 100%;
}

.card img {
  width: 70%;
  height: 90%;
}

/* SEGUNDA PARTE */

.nuevo-div {
  display: flex;
  align-items: center;
  width: 100%;
}

.div-img-dada {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background: linear-gradient(50deg, #6b3636, #ac2121);
  width: 100%;
  height: 600px;
}

.div-img-dada img {
  border-radius: 15px;
  margin-left: 150px;
  height: 200px;
}

.div-texto-dada {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 100px;
  color: white;
  font-family: "Amatic SC", cursive;
}

.div-texto-dada p {
  margin-bottom: 80px;
  font-size: 1.5em;
  font-family: "Amatic SC", cursive;
}

.div-texto-dada h1 {
  margin-bottom: 80px;
  font-size: 5em;
  font-family: "Amatic SC", cursive;
}

.linkCard {
  text-decoration-line: none;
}
.divContactos {
  display: flex;
}

.divContactos span {
  color: black;
}

.divContactos p {
  font-size: 1.5em;
  margin-left: 25px;
}

.divReserva {
  display: flex;
}

.linkCard {
  text-decoration: none;
}
