* {
  box-sizing: border-box;
}

.divGeneralCarrito {
  background-image: url("../img/Fondo.jpg");
  height: 900px;
}

.divCarrito {
  display: inline-block;
  align-items: center;
  background-color: rgba(158, 22, 22, 0.733);
  width: 100%;
  color: white;
  margin-top: 150px;
  padding: 25px;
  border-radius: 20px;
}
.divCarrito h1 {
  display: flex;
  justify-content: center;
  font-family: "Lobster Two", cursive;
  font-size: 3em;
  margin-top: 2px;
  margin-bottom: 5px;
  margin-bottom: 20px;
  color: white;
}

.divNombre {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
}

.divCarrito h3 {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  margin: 10px;
  margin-left: 30px;
  margin-top: 15px;
  color: #9e2e2e;
  font-size: 1.7em;
  font-family: "Great Vibes", cursive;
}

.divCantidad span {
  margin-left: 10px;
  border: none;
  outline: none;
  color: white;
  font-size: 18px;
}

.divCarrito span {
  margin-right: 80px;
  color: black;
  margin-left: 80px;
  font-family: "Bebas Neue", cursive;
  font-size: 1.1em;
  margin-top: 0;
}

.cantidad span {
  margin-right: 5;
}

.divProducto .precio {
  color: #9e2e2e;
  margin-left: 12px;
  font-size: 1.2em;
}

.divProducto .precioTotal {
  color: #9e2e2e;
  margin-left: 12px;
  font-size: 1.2em;
}

.divCarrito input {
  font-family: "Great Vibes", cursive;
  color: #9e2e2e;
  font-size: 2em;
  width: 85px;
  border: none;
  background-color: white;
  margin-right: 18px;
}

.divProducto {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  font-size: 1.2em;
  margin-bottom: 15px;
  width: 100%;
  height: 80px;
  border-radius: 10px;
}
.divProducto:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 10px #000000a4;
}
.divBoton {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.divBoton .eliminar {
  border: none;
  color: #9e2e2e;
}

.eliminar {
  margin-left: 90px;
  margin-right: 15px;
  height: 40px;
}

.vaciar {
  display: flex;
  align-items: center;
  justify-content: center;
}
.vaciar button {
  background-color: #9e2e2e00;
  color: white;
  border: none;
  font-family: "Lobster Two", cursive;
  font-size: 2em;
  margin-bottom: 15px;
}
.vaciar button:hover {
  transform: translateY(-2px);
  box-shadow: 0px 9px 9px #b4121200;
}

.spanPrecios {
  margin: 0px;
}

.montoTotal {
  display: flex;
  align-items: center;
  justify-content: center;
}

.montoTotal h1 {
  font-size: 3.5em;
  margin-right: 100px;
}
